html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Karla", sans-serif;
  background: rgb(9,9,121);
background: linear-gradient(90deg, rgba(9,9,121,1) 2%, rgba(2,0,36,1) 53%, rgba(0,212,255,1) 100%);

  /* Add the blur effect */

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
hr {
    border: 0;
    height: 1px;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
   

  }
  
  .container-box {
    width: 80%;
    background-color: transparent;
    position: relative;
    z-index: 9;
  }
  
  .particles-box {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }
.logo {
    width: 20%;
  }
  
  .nav-theme {
    background-color: #101018f8;
    font-size: 20px;
  }
  
  .animate-navbar {
    box-shadow: 1px 1px 1px #222;
    -webkit-animation: moveDown 0.5s ease-in-out;
            animation: moveDown 0.5s ease-in-out;
  }
  
  @-webkit-keyframes moveDown {
    from {
      -webkit-transform: translateY(-5rem);
              transform: translateY(-5rem);
    }
    to {
      -webkit-transform: translateY(0rem);
              transform: translateY(0rem);
    }
  }
  
  @keyframes moveDown {
    from {
      -webkit-transform: translateY(-5rem);
              transform: translateY(-5rem);
    }
    to {
      -webkit-transform: translateY(0rem);
              transform: translateY(0rem);
    }
  }
.custom-img {
    height: 100vh;
    object-fit: cover;
  }
.about {
    background-color: transparent;
    font-size: 1.1em;
  }
  
  .profile {
    max-width: 18em;
  }
  
  .my-details {
    background-color: rgba(255, 255, 255, 0.747);
  }
.focus {
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
  }
  
  .focus:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  
  .card-title {
    font-size: 2em;
    font-weight: 700;
  }
  
  .image-style {
    width: 3em;
  }
  
  .font-details {
    font-size: 3em;
    font-weight: 500;
    color: #231d5af8;
  }
  
  .font-details-b {
    font-size: 3em;
    font-weight: 500;
    color: #ffffff;
  }


.jumbostyle{
    background: rgb(4,107,99);
background: linear-gradient(71deg, rgba(4,107,99,1) 1%, rgba(2,22,50,1) 80%);
    
  }

  .custom-img {
 
    object-fit:cover;
